}

.main-mapa-espace {
    border-radius: 1rem;
    position: relative;
    height: auto;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
    overflow: hidden;
    width: 60%;
}

.main-mapa-titulo {
    font-size: 2.5rem;
    color: var(--blanco);
    font-weight: bold;
    text-shadow: 2px 2px 50px rgba(0, 0, 0, 0.8);
