}

.main-mapa-subtitulo {
    font-size: 1.2rem;
    color: var(--blanco);
    text-shadow: 2px 2px 50px rgba(0, 0, 0, 0.8);
    -webkit-text-stroke: 1px var(--blanco);
}

.mapa-prueba {
    width: 100%;
    height: auto;
    background-position: center;
    background-attachment: fixed;
    cursor: pointer;
    -webkit-filter: brightness(100%);
            filter: brightness(100%);
    border-radius: 1rem;
    border: .1rem solid var(--blanco);
    box-shadow: 2px 2px 4px var(--primario);
    -webkit-filter: saturate(1.5);
            filter: saturate(1.5);
    transition: .3s ease-in-out;
}

.contenedor-img {
    display: flex;
    justify-content: center;
    align-items: center;
}

.contenedor-img .texto-img {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    visibility: hidden;
    font-weight: bold;
    color: var(--primario);
    padding: 1rem;
    font-size: 1.4rem;
    font-weight: bold;
    border-radius: 1rem;
    text-shadow: 2px 2px 50px rgba(0, 0, 0, 0.8);
    transition: .3s ease-in-out;
}
.main-mapa-espace {
    transition: .3s ease-in-out;
}
.contenedor-img {
    transition: .3s ease-in-out;
}
.main-mapa-espace:hover .texto-img {
    visibility: visible;
    opacity: .8;
    background-color: rgba(255, 255, 255, .8);
}

.main-mapa-espace:hover .mapa-prueba {
    -webkit-filter: blur(1px);
            filter: blur(1px);
}

