.main-books {
    background-color: var(--primario);
    margin: 0rem;
    width: 100%;
    padding-bottom: 1rem;
}

.books {
    flex-direction: row;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 0rem 3rem;
}

.main-books h2 {
    color: var(--blanco);
    text-align: center;
    font-size: 2rem;
    margin-bottom: 1rem;
}

.book {
    /*flex: 1 0 24rem;*/
    width: 30rem;
    height: 15rem;
    margin: .8rem;
    background-color: var(--blanco);
    border-radius: .5rem;
    padding: 0rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    border: .1rem solid var(--blanco);
    transition: 0.3s;
}

.book:hover {
    box-shadow: 2px 2px 8px var(--negro);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

.book-cover:hover .book-img {
    -webkit-filter: blur(2px);
            filter: blur(2px);
}

.book .book-cover {
    position: relative;
    height: auto;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
    overflow: hidden;
    width: 35%;
}

.book-cover-message {
    border-radius: 5rem;
}

.book-cover {
    border-radius: .5rem;
}

.book .book-cover img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: .5rem;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
}

.book-cover img:hover {
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
}

.book .book-cover-message {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    font-weight: bold;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: var(--blanco);
    padding: 10px;
    font-size: 1rem;
    opacity: 0;
    transition: 0.3s;
}

.book-cover:hover .book-cover-message {
    opacity: 1;
    border-radius: .5rem;
}

.book .book-details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 65%;
    padding: 1rem 0.5rem;
}

.book .book-title {
    color: var(--primario);
    font-weight: bold;
    font-size: 1.2rem;
    padding-bottom: 1rem;
}

.book .book-description {
    color: var(--secundario);
    font-size: 1rem;
    width: 100%;
    height: auto;
}

@media (max-width: 850px) {
